/* Designing for scroll-bar */
::-webkit-scrollbar {
   width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
   background: #223c47;
   /* border-radius: 5px;  */
}

/* Handle */
::-webkit-scrollbar-thumb {
   background: #8ebed4;
   border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
   background: #496775;
}
